import React, { useEffect, useState } from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import * as S from "../pagesStyles/blog.styles"
import NProgress from "nprogress"

const Blog = () => {
  const [postList, setPostList] = useState([])
  const [isLoading, setIsLoading] = useState(false)

  // const URL = `https://dev.to/api/articles/latest?username=guiteixeira`
  // const URL = "https://dev.to/api/articles/latest/guiteixeira"
  // const URL = "https://dev.to/api/articles/me/published"
  const URL = "https://dev.to/api/articles?username=guiteixeira"
  const ENV = process.env.API_KEY

  useEffect(() => {
    console.log("API_KEY:")
    console.log(ENV)
  }, [ENV])

  useEffect(() => {
    const fetchPosts = async () => {
      setIsLoading(true)
      NProgress.start()
      await fetch(URL, {
        headers: {
          // accept: "application/vnd.forem.api-v1+json",
          // "api-key": ENV,
          // "Content-Type": "application/json",
        },
      })
        .then((response) => response.json())
        .then((resultData) => {
          setPostList(resultData)
        })

      setIsLoading(false)
      NProgress.done()
    }
    fetchPosts()
  }, [])

  if (isLoading && !postList) {
    return <h1>Estou carregandersons</h1>
  } else {
    return (
      <Layout>
        <Seo title="Articles" />
        <S.Content>
          <h1>Articles</h1>
          <div>
            {postList.map(
              (
                {
                  url,
                  title,
                  readable_publish_date,
                  reading_time_minutes,
                  description,
                },
                index
              ) => (
                <div key={index}>
                  <S.PostLink
                    href={url}
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    <S.SimpleHeader>{title}</S.SimpleHeader>
                  </S.PostLink>
                  <div>
                    <S.ArticleDate>{readable_publish_date}</S.ArticleDate>
                    <S.ReadingTime>
                      {" "}
                      - {reading_time_minutes} min read
                    </S.ReadingTime>
                  </div>
                  <p>{description}</p>
                </div>
              )
            )}
          </div>
        </S.Content>
      </Layout>
    )
  }
}

export default Blog
